.display{
	height: 35%;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: flex-end;
	margin: 0 1em;
	text-align: right;

	
}
.display::after {
	content: "";
	width: 100%;
	height: 4px;
	margin: .5em auto 1em;
	background: var(--clr-accent);
	padding: 0;
}

.current {
	width: 100%;
	/* height: 70%; */
	font-size: 2rem;
	overflow: scroll;

	/* padding: .5em 0 0; */

}
.previous { opacity: 0.5; }